<template>
  <div class="wraper">
    <NavBar></NavBar>
    <div class="content friends">
      <p class="title">:</p>
      <div class="f-list">
        <a
          class="f-item"
          v-for="item in lists"
          :href="item.url"
          target="_blank">
          <div class="avatar" :style="{'background-image': 'url('+ item.avatar + ')'}"></div>
          <span class="line"></span>
          <div class="des">
            <p class="name">{{ item.name }}</p>
            <p>{{ item.des }}</p>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '../components/Nav'
export default {
  data () {
    return {
      lists: [
        {
          name: '',
          url: 'http://funnycoder.lofter.com',
          des: 'Coder~',
          avatar: 'http://imgsize.ph.126.net/?imgurl=http://imglf2.ph.126.net/80tgwfppFxSJbTboaJ4R2Q==/6631977061815991766.jpg_96x96x0x90.jpg'
        },
        {
          name: '',
          url: 'http://zhaoyuxiang.cn',
          des: ', ~',
          avatar: 'https://ooo.0o0.ooo/2016/12/20/5858bbde6e8ac.jpg'
        },
        {
          name: '',
          url: 'https://blog.wanan.me',
          des: '~ ',
          avatar: 'http://www.gravatar.com/avatar/7151791be6ac667ad65ba0a3ba7fd99c?s=250&d=mm&r=x'
        },
        {
          name: '',
          url: 'http://noder.club',
          des: 'node',
          avatar: 'https://ws3.sinaimg.cn/large/7d051389gw1fbdeilrksgj202s02st8i.jpg'
        },
        {
          name: '',
          url: '',
          des: '',
          avatar: 'http://ofyaji162.bkt.clouddn.com/nightcat.jpg'
        },
        {
          name: '',
          url: '',
          des: ', ',
          avatar: 'http://ofyaji162.bkt.clouddn.com/can.jpg'
        },
        {
          name: '_',
          url: 'https://yinmazuo.github.io',
          des: '',
          avatar: 'http://ofyaji162.bkt.clouddn.com/yin.jpg'
        }
      ]
    }
  },
  computed: {
  },
  components: {
    NavBar
  }
}
</script>

<style lang="scss">
.friends {
  min-height: 1000px;
  background-image: url('http://ofyaji162.bkt.clouddn.com/bg1.webp');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;

  .title {
    margin: 50px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.9);
  }

  .f-list {
    position: relative;
    width: 80%;
    height: auto;
    margin: auto;
  }

  .f-item {
    position: relative;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    display: inline-block;

    .avatar {
      margin: auto;
      width: 120px;
      height: 120px;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-size: cover;
    }

    .line {
      position: absolute;
      top: 119px;
      left: 50%;
      margin-left: -20px;
      width: 20px;
      height: 80px;
      border-radius: 30%;
      border-right: 2px solid rgba(128, 64, 0, 0.9);
      z-index: 1;
    }

    .des {
      position: absolute;
      top: 201px;
      left: 50%;
      margin-left: -90px;
      width: 180px;
      background-color: white;
      color: black;
      border-radius: 5px;
      padding: 5px;
      font-size: 14px;

      .name {
        font-size: 16px;
        text-align: center;
        line-height: 30px;
      }
    }
  }
}
</style>



